.home {
  display: flex;
  justify-content: center;
  /* max-width: 1500px; */
  width: 98.5vw;
  margin-left: auto;
  margin-right: auto;
  background-color: #e3e6e6;
}

.bg_image {
  width: 98.5vw;
  z-index: -1;
  margin-bottom: -25%;
  mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 0.85),
    rgba(0, 0, 0, 0)
  );
}

.home_container {
  width: 98.5vw;
}
